/* Styling a login form/container. It centers content, space, and borders and padding */

.login {
    text-align: center;
    justify-content: center;
    display: center;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ccc;
    padding: 20px;
    margin: 20px;
    margin-bottom: 10px;
  }
  
  .login-header {
    border-bottom: 1px solid #cccccc;
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
  
  .input-container {
    margin-top: 20px;
  }
  
  .input-container input {
    margin: 5px;
    padding: 10px;
  }

.center-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 3px;
  border-color: pink;
}

.container {
  border: 1px solid #ccc;
  padding: 20px;
  text-align: center;
  max-width: 600px; 
}

.container label,
.container input,
.container button {
  display: block;
  margin-bottom: 10px;
  border-radius: 3px;
}

  